.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; }
	.slick-next, .slick-prev {

			background: none;
			border: none;
			color: transparent;
			position: absolute;
			z-index: 200; // m ore than the cycle default

			top: 50%;
			
			transform:translateY(-50%);
			cursor: pointer;
			




			&:before {
				color: @skip-button-color;
				.fa-icon();
				font-size: 3em;
				text-shadow: 1px 1px 2px rgba(59, 69, 94, 0.23);
				
			}
		}

		.slick-next {
			right: @skip-offset;
			right: 15vw;
			&:before {
				content: @carousel-next-fa-icon;
		
			}
		}

		.slick-prev {
			left: @skip-offset;
			left: 15vw;
			&:before {
				content:  @carousel-prev-fa-icon;
			}
		}


/* Dots */


.slick-dots { position: absolute; bottom: 5%; list-style: none; display: block; text-align: center; padding: 0; width: 100%; }
.slick-dots li { position: relative; display: inline-block; height: 20px; width: 20px; margin: 0 5px; padding: 0; cursor: pointer; }
.slick-dots li button { border: 0; background: transparent; display: block; height: 20px; width: 20px; outline: none; line-height: 0; font-size: 0; color: transparent; padding: 5px; cursor: pointer; }
.slick-dots li button:hover, .slick-dots li button:focus { outline: none; }
.slick-dots li button:hover:before, .slick-dots li button:focus:before { opacity: 1; }
.slick-dots li button:before { text-shadow:0px 2px 0px #000; position: absolute; top: 0; left: 0; content: "•"; width: 20px; height: 20px; font-size: 36px; line-height: 20px; text-align: center; color: white; opacity: 0.25; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }
.slick-dots li.slick-active button:before { color: white; opacity: 0.75; }

