.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-next,
.slick-prev {
  background: none;
  border: none;
  color: transparent;
  position: absolute;
  z-index: 200;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
.slick-next:before,
.slick-prev:before {
  color: rgba(255, 255, 255, 0.8);
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
