.right-content table,
.left-content table {
  width: 100%;
  table-layout: fixed;
}
.right-content table td:first-of-type:nth-last-of-type(6n),
.left-content table td:first-of-type:nth-last-of-type(6n) {
  background: rgba(0, 134, 172, 0.5);
}
.right-content table tr:first-of-type,
.left-content table tr:first-of-type {
  background: #ba272d;
  color: #dddddd;
  min-height: 0;
}
.right-content table tr:first-of-type td:first-of-type:nth-last-of-type(6n),
.left-content table tr:first-of-type td:first-of-type:nth-last-of-type(6n) {
  background: #ffffff;
}
.right-content table tr:nth-of-type(even),
.left-content table tr:nth-of-type(even) {
  background: rgba(0, 0, 0, 0.1);
}
.right-content table td,
.left-content table td {
  padding: .5em;
  width: 20%;
  height: 80px;
  vertical-align: top;
  text-align: left;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.right-content table td:last-of-type,
.left-content table td:last-of-type {
  border-right: none;
}
.right-content table td.today,
.left-content table td.today {
  background: rgba(65, 165, 53, 0.3);
}
.table-prev,
.table-next {
  display: none;
}
.table-wrapper {
  position: relative;
}
.table-wrapper td:first-of-type:nth-last-of-type(6n) {
  display: none;
}
.table-wrapper .label-column {
  width: 20%;
  position: absolute;
  left: 0;
  top: 0;
  margin: 0 auto;
  overflow: hidden;
}
.table-wrapper .label-column table {
  width: 7000%;
  margin: 0;
}
.table-wrapper .label-column td:first-of-type:nth-last-of-type(6n) ~ td {
  width: 14%;
}
.table-wrapper .label-column td:first-of-type:nth-last-of-type(6n) {
  background: rgba(0, 134, 172, 0.5);
  display: table-cell;
  width: 0.99%;
}
.table-wrapper .label-column tr:first-of-type td {
  background: #ffffff;
}
.table-wrapper .label-column + .table-viewport {
  width: 70%;
  left: 20%;
  position: relative;
}
@media screen and (max-width: 800px) {
  .table-wrapper .label-column {
    left: 5%;
  }
  .table-wrapper .label-column + .table-viewport {
    left: 10%;
  }
}
@media screen and (max-width: 800px) {
  .no-touch .table-wrapper:after {
    content: "";
    width: 100%;
    position: absolute;
    bottom: 0;
    height: 17px;
    background: white;
    left: 0;
    right: 0;
  }
}
@media screen and (max-width: 800px) {
  .table-wrapper {
    width: 100%;
    position: relative;
    display: block;
  }
  .table-wrapper .table-prev,
  .table-wrapper .table-next {
    position: absolute;
    top: 0;
    bottom: 0;
    border: none;
    display: block;
    width: 5%;
    background: #444444;
    color: #dddddd;
  }
  .table-wrapper .table-prev i.fa,
  .table-wrapper .table-next i.fa {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .table-wrapper .table-prev {
    left: 0%;
  }
  .table-wrapper .table-next {
    right: 0%;
  }
  .table-wrapper .table-viewport {
    display: block;
    width: 90%;
    margin: auto;
    position: relative;
    overflow-x: scroll;
  }
  .table-wrapper .table-viewport table {
    width: 500%;
    table-layout: fixed;
    overflow-x: scroll;
    margin: 0;
  }
  .table-wrapper .table-viewport table::-webkit-scrollbar {
    display: none;
  }
  .table-wrapper .table-viewport table tr {
    width: 100%;
    position: relative;
  }
  .table-wrapper .table-viewport table tr td {
    width: 20%;
    padding: .5em 1%;
  }
  .table-wrapper .table-viewport table td:first-of-type:nth-last-of-type(6n) {
    display: none;
  }
}
